#general_page form {
  margin: 0px auto;
  box-sizing: border-box;
  padding: 10px;
  background: #f6f6f6 !important;
  text-align: left;
  border: 1px solid #bbb;
  width: 100%;
}
#general_page input[type="text"],
#general_page input[type="password"],
#general_page input[type="date"],
#general_page input[type="datetime"],
#general_page input[type="email"],
#general_page input[type="number"],
#general_page input[type="search"],
