#general_page select {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  font-size: 15px;
  height: auto;
  margin: 0;
  outline: 0;
  padding: 10px;
  width: 100%;
  background-color: #fff;
  color: #757575;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.03) inset;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
}
#general_page input[type="radio"],
#general_page input[type="checkbox"] {
  margin: 0 4px 8px 0;
}

#general_page select {
  padding: 6px;
  height: 32px;
  border-radius: 2px;
}

#general_page fieldset {
  margin-bottom: 0px;
  border: none;
}

#general_page legend {
  font-size: 1.4em;
  margin-bottom: 10px;
}

