#sales table {
  border: none;
  border-collapse: collapse;
}

#sales table td {
  border-left: 1px solid #f0eeee;
  border-right: 1px solid #f0eeee;
  border-top: none;
  border-bottom: none;
}
#sales table th {
  font-weight: bold;
}

#sales table td:first-child {
  border-left: none;
}

#sales table td:last-child {
  border-right: none;
}
